.item {
    display: flex;
    align-items: center;
    padding: 10px 10px;
    margin-bottom: 10px;
    background-color: #ffffff;
    box-shadow: 0px 4px 3px 0px rgb(133, 133, 133);
    border-radius: 5px;
    transition: box-shadow 200ms;
}

.item:focus,
.item:hover {
    box-shadow: 0px 4px 3px 0px #000;
}

.status {
    width: 25px;
    height: 25px;
    margin-right: 10px;
    border-radius: 50%;
}

.online {
    background-color: green;
}

.ofline {
    background-color: red;
}

.avatar {
    width: 100px;
    background-color: rgb(208, 215, 221);
    border-radius: 8px;
}

.name {
    padding-left: 20px;
    font-size: 28px;
    font-weight: 500;
}