.transactionHistory {
    background-color: white;
    margin: 15px;
    box-shadow: 0px 2px 1px rgba(0, 0, 0, 0.2), 0px 1px 1px rgba(0, 0, 0, 0.14),
      0px 1px 3px rgba(0, 0, 0, 0.12);
    border-radius: 4px;
    
  }
  
  tr:nth-child(even) {
    background: #e9e9e8;
  }
  
  .head {
    background-color: rgb(159, 222, 238);
    text-transform: uppercase;
    
  }
  
  .headItem {
    padding: 15px 60px;
    border: thick double #32a1ce;
  }
     
  .bodyItem {
    padding: 15px 80px;
    text-align: center;
  }